import { useContext } from "react"

import { Button } from "reactstrap"

import { ScenarioContext } from "../../contexts/ScenarioContext"

const RadioGroup = ({ id, options }) => {
    const { isChecked, setChecked, toggleIntvAnswer } = useContext(ScenarioContext)

    const changeHandler = (event, option) => {
        const val = event.currentTarget.value
        const otherOptions = options.map(opt => opt.id).filter(_opt => _opt !== val)
        otherOptions.forEach(opt => setChecked(opt, false))
        toggleIntvAnswer(option)
        setChecked(id, true)
    }
    const clearHandler = () => {
        const ids = options.map(opt => opt.id).concat(id)
        ids.forEach(_id => {
            setChecked(_id, false)
        })
    }

    return (
        <>
        <br />
        <span>
        {options.map(opt => (
            <label
                key={opt.id}
                className="prompt-option"
            >
                <input
                    type="radio"
                    className="prompt-radio-btn"
                    name={`${id}-options`}
                    value={opt.id}
                    checked={isChecked(opt.id)}
                    onChange={(event) => changeHandler(event, opt)}
                />
                {opt.label}
            </label>
        ))}
            <Button
                color="primary"
                size="sm"
                outline={true}
                onClick={clearHandler}
                className="clear-radio-btn"
            >
                Clear
            </Button >
        </span>
        </>
    )
    // return (
    //     <ul>
    //         {options.map(opt => (
    //             <li key={opt.id}>
    //                 <label>
    //                     <input
    //                         type="radio"
    //                         name={name}
    //                         value={opt.id}
    //                         checked={isChecked(opt.id)}
    //                         onChange={(event) => changeHandler(event, opt)}
    //                     />
    //                     &nbsp; {opt.label} &nbsp;
    //                 </label>
    //             </li>
    //         ))}

    //         <li><Button color="primary" size="sm" outline={true} onClick={clearHandler} > Clear</Button ></li>
    //     </ul>
    // )
}

export default RadioGroup