import React from "react"

import {
    HashRouter as Router,
    Switch,
} from "react-router-dom"

import "bootstrap/dist/css/bootstrap.min.css";

import { AuthProvider, PrivateRoute } from "@meddbriefer/mdb-auth"

import { PreferencesProvider } from "./contexts/PreferencesContext";


import "./styles.css";

import Scenario     from "./routes/Scenario"
import ScenarioList from "./routes/ScenarioList"

import UnauthedLayout from "./components/layouts/UnauthedLayout"

const App = (props) => {
    return (
        <AuthProvider>
            <PreferencesProvider>
                <Router>
                    <Switch>
                        <PrivateRoute
                            path="/scenario/:scenarioId"
                            authFlowLayout={UnauthedLayout}
                            component={Scenario}
                        />
                        // default (catchall) route
                        <PrivateRoute
                            exact path="/"
                            authFlowLayout={UnauthedLayout}
                            component={ScenarioList}
                        />
                    </Switch>
                </Router>
            </PreferencesProvider>
        </AuthProvider>
    )
}


export default App;