import { useState, useEffect } from "react"

import {db} from "@meddbriefer/mdb-firebase"

import { ScenarioProvider } from "../contexts/ScenarioContext"

import ScenarioLayout from "../components/layouts/ScenarioLayout"

import scenarioData from "@meddbriefer/scenario-data/strawManV2.json"

const getDefaultValues = (scen) => {
    return {
        vitals: scen.initialVitalSigns,
        vitalsRecomputed: false,
        checkListItems: {},
    }
}

/*
 * returns an array of interventions associated with system, both filtered and ordered
 * by filteredIDs
 */
const getSystemIntvIds = (scen, filteredIDs, system) => {
    const systemIntvIDs = scen.systemInterventions[system].intvs.map(rec => rec.id)
    return filteredIDs.filter(fID => systemIntvIDs.includes(fID))
}

const Scenario = ({scenarioId}) => {
    const [scenario, setScenario] = useState(null)
    window.scenario = scenario
    const [initValues, setInitValues] = useState(null)
    const [ready, setReady] = useState(false)

    useEffect(
        () => {
            const loadScen = () => {
                //ignore scenarioId for now importing hardcoded scen file
                setScenario(scenarioData)
                const defaults = getDefaultValues(scenarioData)
                setInitValues(defaults)
            }
            loadScen()
        }, []
    )

    useEffect(
        () => {
            if(!!scenario) {
                db.collection("scenario-required-actions")
                    .where("scenarioName", "==", "M2CA")
                    .limit(1)
                    .get()
                    .then((snapShot) => {
                        // need to do a forEach, but doc array only contains
                        // 1 doc
                        snapShot.docs.forEach((doc) => {
                            const data = doc.data()
                            //hard code in some missing stuff
                            data.associations["prompt-num-ivs"] = "answer-num-ivs-two"
                            data.associations["prompt-iv-size"] = "answer-iv-size-large-as-possible"

                            scenario.correctPromptAnswers = JSON.parse(JSON.stringify(data.associations))
                            scenario.criticalActions = JSON.parse(JSON.stringify(data.criticalActions))
                        })
                        setReady(true)
                    })
            }
        }, [scenario]
    )
    useEffect(
        () => {
            if (!!scenario && scenario.criticalActions) {
                // console.log("criticalActions", scenario.criticalActions)
                const minimalIDs = scenario.criticalActions.map(rec => rec.minimal).flat().map(rec => rec.id)
                const standardIDs = scenario.criticalActions.map(rec => rec.standard).flat().map(rec => rec.id)
                // console.log("minimalIDs", minimalIDs)
                const SYSTEM_IDS = ["airway", "breathing", "bleeding", "shock"]
                let systems = {}
                SYSTEM_IDS.forEach(system => {
                    systems[system] = {
                        requiredInterventions: []
                    }
                    const systemIntvs = getSystemIntvIds(scenario, standardIDs, system)
                    systemIntvs.forEach(intv => {
                        const intvForm = scenario.interventionForms[intv]
                        let intervention = {
                            id: intv,
                            label: intvForm.label,
                            prompts: []
                        }
                        intvForm.prompts.forEach(prompt => {
                            const allOptions = prompt.options.map(rec => ({id: rec.id, label: rec.label}))
                            if (prompt.id in scenario.correctPromptAnswers) {
                                const tmp = scenario.correctPromptAnswers[prompt.id]
                                const correctAnswers = Array.isArray(tmp) ? tmp : new Array(tmp)

                                intervention.prompts.push({
                                    id: prompt.id,
                                    label: prompt.label,
                                    correctOptions: correctAnswers,
                                    allOptions: allOptions})
                            } else {

                                intervention.prompts.push({id: prompt.id, correctOptions: ["UNKNOWN"], allOptions: allOptions})
                            }
                        })
                        systems[system].requiredInterventions.push(intervention)
                    })
                })
                console.log(JSON.stringify(systems, null, 4))
                setReady(true)
            }
        }, [scenario]
    )
    return (
        !ready
        ? <div>Loading scenario...</div>
        :
        <ScenarioProvider
            scenario={scenario}
            defaultValues={initValues}
        >
            <ScenarioLayout />
        </ScenarioProvider>
    )
}

export default Scenario