
import classnames from "classnames"

import { useHistory } from "react-router-dom"

import { useScenario } from "../contexts/ScenarioContext"

import SubPhaseNav from "./SubPhaseNav"

const PhaseNav = ({baseURL, activeColor, id, label, subPhases = []}) => {
    const { currPhase, setCurrPhase, setCurrSubPhase } = useScenario()
    const history  = useHistory()

    const clickHandler = (event) => {
        setCurrSubPhase(null)
        setCurrPhase(id)
        history.push(`${baseURL}/${id}`)
    }

    if (subPhases.length > 0) {
        return (
            <div
                className="phase-nav"
                style={{
                    backgroundColor: (currPhase === id) ? activeColor : "transparent"
                }}
            >
                <div className="phase-label">
                    <button
                        id={id}
                        onClick={clickHandler}
                        style={{
                            color: (currPhase === id) ? "black" : "white",
                        }}
                    >
                        {label}
                    </button>
                </div>
                {subPhases.map(sph=> (
                    <SubPhaseNav
                        key={sph.id}
                        baseURL={baseURL}
                        activeColor={activeColor}
                        id={sph.id}
                        label={sph.label}
                        phase={sph.phase}
                    />
                ))}
            </div>
        )
    }
    return (
        <div
            className="phase-nav no-children"
            style={{ backgroundColor: (currPhase === id) ? activeColor : "transparent" }}
        >
            <button
                id={id}
                onClick={clickHandler}
                style={{
                    color: (currPhase === id) ? "black" : "white",
                }}
            >
                {label}
            </button>
        </div>
    )
}

export default PhaseNav