import { useContext, useEffect } from "react"

import { ScenarioContext } from "../../contexts/ScenarioContext"

import FormCheckBox from "./FormCheckBox"

const CheckBoxGroup = ({ id, options }) => {
    const { checkListItems, isChecked, setChecked, someChecked, toggleIntvAnswer } = useContext(ScenarioContext)

    const clickHandler = (option) => {
        // console.log(option)
        toggleIntvAnswer(option)
    }

    useEffect(() => {
        if (someChecked(options.map(opt => opt.id))) {
            if (!isChecked(id)) {
                setChecked(id, true)
            }
        } else {
            if (isChecked(id)) {
                setChecked(id, false)
            }
        }
    }, [checkListItems, options, setChecked, someChecked])

    return (
        <ul className="checkbox-group-options">
            {options.map(opt => (
                <li
                    key={opt.id}
                    className="prompt-option"
                >
                    <FormCheckBox
                        id={opt.id}
                        label={opt.label}
                        clickHandler={() => clickHandler(opt)}
                    />
                </li>
            ))}
        </ul>
    )
}

export default CheckBoxGroup