import { useHistory } from "react-router-dom"

import { useScenario } from "../contexts/ScenarioContext"

const SubPhaseNav = ({ baseURL, activeColor, id, label, phase}) => {
    const {setCurrPhase, currPhase, currSubPhase, setCurrSubPhase } = useScenario()
    const history = useHistory()

    const clickHandler = (event) => {
        // console.log("phase", phase)
        // console.log("subphase", id)
        setCurrPhase(phase)
        setCurrSubPhase(id)
        history.push(`${baseURL}/${id}`)
    }

    // #566573
    return (
        <div
            className="subphase-nav"
        >
            <button
                id={id}
                style={{
                    color: (currSubPhase === id | currPhase !== phase) ? "white" : "black",
                    backgroundColor: (currSubPhase === id) ? "black" : "transparent"
             }}
                onClick={clickHandler}>
                {label}
            </button>
        </div>
    )
}

export default SubPhaseNav