import { useEffect, useState } from "react"

import { Button, Table, Nav, NavItem, NavLink, TabContent, TabPane } from "reactstrap"

import { useScenario } from "../../contexts/ScenarioContext"

import { INTERV } from "@meddbriefer/scenario-data/constants.js"

// import RevealTable from "../RevealTable"
import classNames from "classnames"

const CATEGORY = "intervention-status"

const InterventionsGroup = ({id, label, intvsPerformed = []}) => {
    const { checkListItems, logPatientStatusRequest } = useScenario()

    const clickHandler = (intvID) => {
        const intervention = checkListItems.find(rec.id === intvID)
        // FIXME: intervention.systemStatus is currently unimplemented so
        // we're logging "unknown"
        logPatientStatusRequest(CATEGORY, intvID, "unknown")
    }
    return (
        <tbody>
        {intvsPerformed.map(intv => (
            <tr key={`intv-performed-${intv.label}`}>
                <td>{intv.label}</td>
                <td>unknown status</td>
                <td>
                    <Button
                        color="success"
                        size="sm"
                        className="request-btn"
                        onClick={() => clickHandler(intv.id)}
                    >
                        Requested
                    </Button>
                </td>
            </tr>
        ))}
        </tbody>

    )
}

const SystemsTable = ({id, label, intvs}) => {
    return (
        <Table size="sm" striped={true} bordered={false}>
            <thead>
                <tr>
                    <th>Intervention</th>
                    <th>{label} Status</th>
                    <th></th>
                </tr>
            </thead>
            {intvs.length > 0 &&
                <InterventionsGroup
                    id={id}
                    label={label}
                    intvsPerformed={intvs}
                />
            }
        </Table>
    )
}
const InterventionsStatus = () => {
    const { scenario, checkListItems, systemsTab, setSystemsTab } = useScenario()
    const [intvsPerformed, setIntvsPerformed] = useState([])

    const isActiveTab = (tab) => tab === systemsTab
    const activateTab = (tab) => {
        if (systemsTab !== tab) setSystemsTab(tab)
    }

    const getSystemIntvsPerformed = () => {
        const recs = Object.values(checkListItems)
            .filter(val => !!val)
            .filter(rec => rec.type === INTERV)
            .filter(rec => !!rec.system)
        recs.sort((a, b) => a.timestamp - b.timestamp)
        // console.log(system, recs)
        return recs
    }

    useEffect(
        () => {
            const intvs = getSystemIntvsPerformed()
            if (intvs !== intvsPerformed) {
                setIntvsPerformed(intvs)
            }
        }, [checkListItems]
    )

    return (
        <>
        <Nav tabs>
            <NavItem>
                <NavLink
                    className={classNames({active: isActiveTab('airway')})}
                    onClick={() => activateTab('airway')}
                >
                    Airway
                </NavLink>
            </NavItem>
            <NavItem>
                <NavLink
                    className={classNames({ active: isActiveTab('breathing') })}
                    onClick={() => activateTab('breathing')}
                >
                    Breathing
                </NavLink>
            </NavItem>
            <NavItem>
                <NavLink
                    className={classNames({ active: isActiveTab('bleeding') })}
                    onClick={() => activateTab('bleeding')}
                >
                    Bleeding
                </NavLink>
            </NavItem>
            <NavItem>
                <NavLink
                    className={classNames({ active: isActiveTab('shock') })}
                    onClick={() => activateTab('shock')}
                >
                    Shock
                </NavLink>
            </NavItem>
        </Nav>
        <TabContent activeTab={systemsTab}>
            <TabPane tabId="airway">
                <SystemsTable
                    id="airway"
                    label="Airway"
                    intvs={intvsPerformed.filter(rec => rec.system === "airway")}
                />
            </TabPane>
            <TabPane tabId="breathing">
                <SystemsTable
                    id="breathing"
                    label="Breathing"
                    intvs={intvsPerformed.filter(rec => rec.system === "breathing")}
                />
            </TabPane>
            <TabPane tabId="bleeding">
                <SystemsTable
                    id="bleeding"
                    label="Bleeding"
                    intvs={intvsPerformed.filter(rec => rec.system === "bleeding")}
                />
            </TabPane>
            <TabPane tabId="shock">
                <SystemsTable
                    id="shock"
                    label="Shock"
                    intvs={intvsPerformed.filter(rec => rec.system === "shock")}
                />
            </TabPane>
        </TabContent>
        </>
    )
}

