import { useState, useEffect } from "react"

import { useParams } from "react-router-dom"

import { useScenario } from "../contexts/ScenarioContext"
import { usePrefs }    from "../contexts/PreferencesContext"

import PhaseLayout        from "../components/layouts/PhaseLayout"

import PhaseCheckList     from "../components/PhaseCheckList"
import PatientStatus      from "../components/PatientStatus"
import Panel              from "../components/Panel"

const Phase = () => {
    const { phaseID } = useParams()
    const { scenario, setCurrIntv, setCurrIntvCat, setShowIntvSelection } = useScenario()
    const { patientStatusDisplay } = usePrefs()
    const [phase, setPhase] = useState(null)

    useEffect(() => {
        setPhase(scenario.phaseData.find(rec => rec.id === phaseID))
    }, [phaseID])

    const getTop = () => <PhaseCheckList section={phase} />

    const getBottom = () => {
        if ("navbar" === patientStatusDisplay) {
            return (<div></div>)
        }
        return (
            <Panel title="Patient Status" collapsable scrollBody>
                <PatientStatus />
            </Panel>
        )
    }

    useEffect(() => {
        /* upon navigating to a new phase:
         - clear the current intervention and category.
         - hide intervention categories
        */
        setCurrIntv(null)
        setCurrIntvCat("")
        setShowIntvSelection(true)
    }, [phase])

    if (!!phase) {
        return (
            <PhaseLayout
                top={getTop(phase)}
                bottom={getBottom()}
            />
        )
    } else {
        return (<div>phase not set yet</div>)
    }
}

export default Phase